import React from "react";
import {action} from "@storybook/addon-actions";
import {AddItemForm} from "../AddItemForm";


export default {
    title: "Task Component",
    component: Task,
}

const checkBoxHandlerCallback = action("Button 'add' been pressed inside tha form")
const onChangeTitleHandlerCallback = action("Button 'add' been pressed inside tha form")
const onRemoveHandlerCallback = action("Button 'add' been pressed inside tha form")

export const TaskmBaseExample = () => {
    return <>
        <Task
            task={{id: "1", isDone: true, title: "CSS"}}
            todoListId={"todoListId1"}
            checkBoxHandler={checkBoxHandlerCallback}
            onChangeTitleHandler={onChangeTitleHandlerCallback}
            onRemoveHandler={onRemoveHandlerCallback}
        />
        <Task
            task={{id: "2", isDone: false, title: "JS"}}
            todoListId={"todoListId2"}
            checkBoxHandler={checkBoxHandlerCallback}
            onChangeTitleHandler={onChangeTitleHandlerCallback}
            onRemoveHandler={onRemoveHandlerCallback}
        />
    </>
};


TaskmBaseExample.parameters = { storySource: { source: "() => {\r\n    return <>\r\n        <Task\r\n            task={{id: \"1\", isDone: true, title: \"CSS\"}}\r\n            todoListId={\"todoListId1\"}\r\n            checkBoxHandler={checkBoxHandlerCallback}\r\n            onChangeTitleHandler={onChangeTitleHandlerCallback}\r\n            onRemoveHandler={onRemoveHandlerCallback}\r\n        />\r\n        <Task\r\n            task={{id: \"2\", isDone: false, title: \"JS\"}}\r\n            todoListId={\"todoListId2\"}\r\n            checkBoxHandler={checkBoxHandlerCallback}\r\n            onChangeTitleHandler={onChangeTitleHandlerCallback}\r\n            onRemoveHandler={onRemoveHandlerCallback}\r\n        />\r\n    </>\r\n}" }, ...TaskmBaseExample.parameters };